.CreateTodoButton{
    background-color: #61dafa;
    box-shadow: 0px 5px 25px rgba(97,218,250,0.5);
    border:none;
    cursor:pointer;
    border-radius: 50%;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color:#fafafa;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;

    transform:rotate(0);
    transition: .3s ease;
}

.CreateTodoButton:hover{
    transform: rotate(360deg);
    background-color: #1686a5;
}